"use client";

import { useState } from 'react';
import { useForm } from 'react-hook-form';
import { zodResolver } from '@hookform/resolvers/zod';
import * as z from 'zod';
import { Button } from '@/components/ui/button';
import { Input } from '@/components/ui/input';
import { Label } from '@/components/ui/label';
import { Textarea } from '@/components/ui/textarea';
import { Checkbox } from '@/components/ui/checkbox';
import { Card, CardContent, CardDescription, CardHeader, CardTitle } from '@/components/ui/card';
import { Alert, AlertDescription } from '@/components/ui/alert';
// Zod schema for form validation
const proyectoSchema = z.object({
  nombreProyecto: z.string().min(1, 'El nombre del proyecto es requerido'),
  entidadFinanciadora: z.string().min(1, 'La entidad financiadora es requerida'),
  region: z.string().optional(),
  sector: z.string().optional(),
  tipoOportunidad: z.string().optional(),
  fase: z.string().optional(),
  responsableUAC: z.string().optional(),
  aliados: z.string().optional(),
  plazo: z.string().optional(),
  montoEstimado: z.string().optional(),
  modalidadFinanciamiento: z.string().optional(),
  apoyoRequerido: z.string().optional(),
  contribucionEjes: z.string().optional(),
  escalabilidad: z.string().optional(),
  sinergias: z.string().optional(),
  nivelInnovacion: z.string().optional(),
  reputacional: z.string().optional(),
  observaciones: z.string().optional(),
  reunionActualizacion: z.string().optional(),
  responsableActualizacion: z.string().optional(),
  incluirEnRadar: z.boolean().optional()
});

type ProyectoFormData = z.infer<typeof proyectoSchema>;

export default function ProyectosForm() {
  const [isSubmitting, setIsSubmitting] = useState(false);
  const [submitMessage, setSubmitMessage] = useState<{ type: 'success' | 'error'; message: string } | null>(null);

  const {
    register,
    handleSubmit,
    formState: { errors },
    reset,
    setValue,
    watch
  } = useForm<ProyectoFormData>({
    resolver: zodResolver(proyectoSchema),
    defaultValues: {
      incluirEnRadar: false
    }
  });

  const incluirEnRadar = watch('incluirEnRadar');

  const onSubmit = async (data: ProyectoFormData) => {
    setIsSubmitting(true);
    setSubmitMessage(null);

    try {
      const response = await fetch('/api/proyectos', {
        method: 'POST',
        headers: {
          'Content-Type': 'application/json',
        },
        body: JSON.stringify(data),
      });

      const result = await response.json();

      if (result.success) {
        setSubmitMessage({
          type: 'success',
          message: 'Proyecto guardado exitosamente'
        });
        reset();
      } else {
        setSubmitMessage({
          type: 'error',
          message: result.message || 'Error al guardar el proyecto'
        });
      }
    } catch (error) {
      setSubmitMessage({
        type: 'error',
        message: 'Error de conexión. Por favor, inténtelo de nuevo.'
      });
    } finally {
      setIsSubmitting(false);
    }
  };

  return (
    <Card className="w-full max-w-4xl mx-auto">
      <CardHeader>
      <div className="flex justify-center">
        <img src="/Logo-CATIE.png" alt="Logo" className="h-16 mb-4" />
      </div>
        <CardTitle className="text-2xl font-bold text-center">Formulario de Proyectos</CardTitle>
        <CardDescription className="text-center">
          Complete la información del proyecto u oportunidad
        </CardDescription>
      </CardHeader>
      <CardContent>
        {submitMessage && (
          <Alert className={`mb-6 ${submitMessage.type === 'success' ? 'border-green-500 bg-green-50' : 'border-red-500 bg-red-50'}`}>
            <AlertDescription className={submitMessage.type === 'success' ? 'text-green-700' : 'text-red-700'}>
              {submitMessage.message}
            </AlertDescription>
          </Alert>
        )}

        <form onSubmit={handleSubmit(onSubmit)} className="space-y-6">
          {/* Información Básica del Proyecto */}
          <div className="grid grid-cols-1 md:grid-cols-2 gap-6">
            <div className="space-y-2">
              <Label htmlFor="nombreProyecto">Nombre / Código del Proyecto *</Label>
              <Input
                id="nombreProyecto"
                {...register('nombreProyecto')}
                className={errors.nombreProyecto ? 'border-red-500' : ''}
              />
              {errors.nombreProyecto && (
                <p className="text-sm text-red-500">{errors.nombreProyecto.message}</p>
              )}
            </div>

            <div className="space-y-2">
              <Label htmlFor="entidadFinanciadora">Entidad financiadora / Donante / Socio estratégico *</Label>
              <Input
                id="entidadFinanciadora"
                {...register('entidadFinanciadora')}
                className={errors.entidadFinanciadora ? 'border-red-500' : ''}
              />
              {errors.entidadFinanciadora && (
                <p className="text-sm text-red-500">{errors.entidadFinanciadora.message}</p>
              )}
            </div>
          </div>

          {/* Ubicación y Sector */}
          <div className="grid grid-cols-1 md:grid-cols-2 gap-6">
            <div className="space-y-2">
              <Label htmlFor="region">Región / País / Enfoque territorial</Label>
              <Input id="region" {...register('region')} />
            </div>

            <div className="space-y-2">
              <Label htmlFor="sector">Sector / Tema estratégico</Label>
              <Input id="sector" {...register('sector')} />
            </div>
          </div>

          {/* Tipo y Fase */}
          <div className="grid grid-cols-1 md:grid-cols-2 gap-6">
            <div className="space-y-2">
              <Label htmlFor="tipoOportunidad">Tipo de oportunidad</Label>
              <Input id="tipoOportunidad" {...register('tipoOportunidad')} />
            </div>

            <div className="space-y-2">
              <Label htmlFor="fase">Fase</Label>
              <Input id="fase" {...register('fase')} />
            </div>
          </div>

          {/* Responsables */}
          <div className="grid grid-cols-1 md:grid-cols-2 gap-6">
            <div className="space-y-2">
              <Label htmlFor="responsableUAC">Responsable UAC</Label>
              <Input id="responsableUAC" {...register('responsableUAC')} />
            </div>

            <div className="space-y-2">
              <Label htmlFor="aliados">Aliados / Socios potenciales</Label>
              <Input id="aliados" {...register('aliados')} />
            </div>
          </div>

          {/* Plazo y Monto */}
          <div className="grid grid-cols-1 md:grid-cols-2 gap-6">
            <div className="space-y-2">
              <Label htmlFor="plazo">Plazo / Deadline</Label>
              <Input id="plazo" {...register('plazo')} type="date" />
            </div>

            <div className="space-y-2">
              <Label htmlFor="montoEstimado">Monto estimado</Label>
              <Input id="montoEstimado" {...register('montoEstimado')} />
            </div>
          </div>

          {/* Modalidad y Apoyo */}
          <div className="grid grid-cols-1 md:grid-cols-2 gap-6">
            <div className="space-y-2">
              <Label htmlFor="modalidadFinanciamiento">Modalidad de financiamiento</Label>
              <Input id="modalidadFinanciamiento" {...register('modalidadFinanciamiento')} />
            </div>

            <div className="space-y-2">
              <Label htmlFor="apoyoRequerido">Apoyo requerido desde DAE</Label>
              <Input id="apoyoRequerido" {...register('apoyoRequerido')} />
            </div>
          </div>

          {/* Campos de texto largo */}
          <div className="space-y-6">
            <div className="space-y-2">
              <Label htmlFor="contribucionEjes">Contribución a ejes institucionales / programas prioritarios</Label>
              <Textarea id="contribucionEjes" {...register('contribucionEjes')} rows={3} />
            </div>

            <div className="space-y-2">
              <Label htmlFor="escalabilidad">Posible escalabilidad regional o multisectorial</Label>
              <Textarea id="escalabilidad" {...register('escalabilidad')} rows={3} />
            </div>

            <div className="space-y-2">
              <Label htmlFor="sinergias">Sinergias con otras iniciativas en curso</Label>
              <Textarea id="sinergias" {...register('sinergias')} rows={3} />
            </div>

            <div className="space-y-2">
              <Label htmlFor="nivelInnovacion">Nivel de innovación o diferenciación</Label>
              <Textarea id="nivelInnovacion" {...register('nivelInnovacion')} rows={3} />
            </div>

            <div className="space-y-2">
              <Label htmlFor="reputacional">Reputacional o posicionamiento institucional</Label>
              <Textarea id="reputacional" {...register('reputacional')} rows={3} />
            </div>

            <div className="space-y-2">
              <Label htmlFor="observaciones">Observaciones / Próximos pasos</Label>
              <Textarea id="observaciones" {...register('observaciones')} rows={4} />
            </div>
          </div>

          {/* Seguimiento */}
          <div className="grid grid-cols-1 md:grid-cols-2 gap-6">
            <div className="space-y-2">
              <Label htmlFor="reunionActualizacion">Reunión de actualización (Fecha tentativa)</Label>
              <Input id="reunionActualizacion" {...register('reunionActualizacion')} type="date" />
            </div>

            <div className="space-y-2">
              <Label htmlFor="responsableActualizacion">Responsable de actualización en sistema</Label>
              <Input id="responsableActualizacion" {...register('responsableActualizacion')} />
            </div>
          </div>

          {/* Checkbox */}
          <div className="flex items-center space-x-2">
            <Checkbox
              id="incluirEnRadar"
              checked={incluirEnRadar}
              onCheckedChange={(checked) => setValue('incluirEnRadar', checked as boolean)}
            />
            <Label htmlFor="incluirEnRadar" className="text-sm font-medium leading-none peer-disabled:cursor-not-allowed peer-disabled:opacity-70">
              Incluir en radar institucional / CRM (En el futuro)
            </Label>
          </div>

          {/* Submit Button */}
          <div className="flex justify-center pt-6">
            <Button 
              type="submit" 
              disabled={isSubmitting}
              className="w-full md:w-auto px-8 py-2"
            >
              {isSubmitting ? 'Guardando...' : 'Guardar Proyecto'}
            </Button>
          </div>
        </form>
      </CardContent>
    </Card>
  );
}
